.logo {
  width: $logo-width;
  height: $logo-height;
  background-image: url("../images/logo.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;

  @media screen and (max-width: 768px) {
    width: $logo-min-width;
    height: $logo-min-height;
    z-index: 3;
  }
}
